import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { TrainingWrapper, DayWrapper } from './Training.style';
import { useParams } from 'react-router-dom';
import { useDatabase } from 'store/DatabaseContext';
import { useAuth } from 'store/AuthContext';
import Header from 'components/atoms/Heaader/Header';
import Button from 'components/atoms/Button/Button';
import AddExercise from 'components/organisms/AddExercise/AddExercise';

const Training = (props) => {
  const { id } = useParams();
  const { getTraining } = useDatabase();
  const { currentUser } = useAuth();
  const [training, setTraining] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const modalId = useRef(null);

  const closeModal = () => setIsModalOpen(false);
  const openModal = () => setIsModalOpen(true);
  const postDataToModal = (id) => {
    modalId.current = id;
    openModal();
  };

  useEffect(() => {
    const getData = async () => {
      try {
        await getTraining(currentUser.uid, id, setTraining);
      } catch (error) {
        console.log(error);
      }
    };

    getData();

    return () => getData;
  }, []);

  const renderDays = training.map((item) => (
    <DayWrapper key={item.name} onClick={() => postDataToModal(item.name)}>
      <Header small>{item.name}</Header>
      {item.exercises &&
        item.exercises.map((item) => (
          <p key={item.exerciseName}>{item.exerciseName}</p>
        ))}
      <Button small>+ dodaj ćwiczenie</Button>
    </DayWrapper>
  ));

  console.log(training);
  return (
    <TrainingWrapper>
      <Header>{id}</Header>
      {training.length > 0 && renderDays}
      {isModalOpen && (
        <AddExercise path={id} id={modalId.current} closeModal={closeModal} />
      )}
    </TrainingWrapper>
  );
};

Training.propTypes = {};

export default Training;
